.inputLabel {
	height: 70px;
	display: block;
	position: relative;
	width: 70%;
 }
 
 .inputRoot {
	border: 1px solid rgb(162, 159, 159);
	border-radius: 5px;
	padding: 10px;
	line-height: 1.2;
	width: 100%;
	position: relative;
 }
 
 .error {
	color: red;
	font-size: 14px;
	position: absolute;
	top: 45px;
	left: 8px;
 }
 
